<template>
  <Experiment title="BA Thesis Experiment">
    <!-- Welcome -->
    <InstructionScreen :title="'Welcome!'">
      <div class="block">
        <p>
          Thank you for taking part in the experiment.
          In this study you will read a series of short stories describing chains of events.
          Each story ends in an outcome.
        </p>

        <p><strong>Your task:</strong></p>
        <p>
          For each story, please select <strong>the event that seems like the best candidate for saying that it caused the outcome</strong>.
        </p>

        <p>
          There are no right or wrong answers, please respond with your best judgment.
        </p>

        <p><em>Click “Next” to begin with the first story.</em></p>
      </div>
    </InstructionScreen>

    <!-- Loading -->
    <InstructionScreen v-if="loading" :title="'Loading…'">
      <div class="block">Preparing the experiment.</div>
    </InstructionScreen>

    <!-- Error -->
    <InstructionScreen v-else-if="errorMessage" :title="'Error'">
      <div class="block">
        <p><strong>Something went wrong:</strong></p>
        <p>{{ errorMessage }}</p>
        <p class="hint">
          Check that the CSV is at <code>public/stimuli/vignettes.csv</code> and that images are in
          <code>public/images/</code> (or use correct paths in the CSV).
        </p>
      </div>
    </InstructionScreen>

    <!-- Trials -->
    <template v-else>
      <template v-for="(trial, idx) in trials" :key="`trial-${idx}`">
        <InstructionScreen :title="screenTitle(trial)">
          <div class="block">
            <div class="imageWrap" v-if="trial.image">
              <img :src="trial.image" alt="Scenario illustration" class="stimImage" />
            </div>

            <p class="readCarefully">Please read the following story carefully.</p>

            <p><strong>Event A:</strong> {{ trial.event_A }}</p>
            <p><strong>Event B:</strong> {{ trial.event_B }}</p>
            <p><strong>Event C:</strong> {{ trial.event_C }}</p>
            <p><strong>Event D:</strong> {{ trial.event_D }}</p>

            <p class="outcome">
              <strong>Outcome:</strong> {{ trial.outcome_shown }}
            </p>

            <hr class="sep" />

            <p>
              From the four events above, please select
              <strong>the event that seems like the best candidate for saying that it caused the outcome</strong>.
            </p>

            <label class="choice">
              <input type="radio" :name="`cause-${idx}`" value="A" v-model="responses[idx].causeChoice" />
              <strong>A:</strong> {{ trial.event_A }}
            </label>

            <label class="choice">
              <input type="radio" :name="`cause-${idx}`" value="B" v-model="responses[idx].causeChoice" />
              <strong>B:</strong> {{ trial.event_B }}
            </label>

            <label class="choice">
              <input type="radio" :name="`cause-${idx}`" value="C" v-model="responses[idx].causeChoice" />
              <strong>C:</strong> {{ trial.event_C }}
            </label>

            <label class="choice">
              <input type="radio" :name="`cause-${idx}`" value="D" v-model="responses[idx].causeChoice" />
              <strong>D:</strong> {{ trial.event_D }}
            </label>

            <p class="hint">
              (If you are unsure, please choose the option that seems the most important).
            </p>
          </div>
        </InstructionScreen>
      </template>

      <!-- Default magpie post test screen -->
      <PostTestScreen />

      <!-- End -->
      <InstructionScreen :title="'Thank you!'">
        <div class="block">
          <p>You have completed the study.</p>
        </div>
      </InstructionScreen>

      <SubmitResultsScreen />
    </template>
  </Experiment>
</template>

<script>
import Papa from "papaparse";

export default {
  name: "App",
  data() {
    return {
      loading: true,
      errorMessage: "",
      trials: [],
      responses: []
    };
  },
  methods: {
    screenTitle(trial) {
      return trial.scenario_title || "Story";
    },

    // stable participant id so counterbalancing stays stable for a participant across reloads
    getParticipantId() {
      const key = "ba_thesis_pid";
      let pid = localStorage.getItem(key);
      if (!pid) {
        pid = `${Date.now()}_${Math.random().toString(16).slice(2)}`;
        localStorage.setItem(key, pid);
      }
      return pid;
    },

    // seeded-ish integer from participant id (simple + stable)
    pidSeed(pid) {
      let h = 0;
      for (let i = 0; i < pid.length; i++) h = (h * 31 + pid.charCodeAt(i)) >>> 0;
      return h;
    },

    // deterministic shuffle using a simple LCG (stable per participant)
    shuffleWithSeed(arr, seed) {
      const a = arr.slice();
      let s = seed >>> 0;
      const rand = () => {
        // LCG constants
        s = (1664525 * s + 1013904223) >>> 0;
        return s / 4294967296;
      };
      for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(rand() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
      }
      return a;
    },

    // build half/half valence pattern (balanced) and shuffle it
    makeValenceArray(n, seed) {
      const half = Math.floor(n / 2);
      const base = [
        ...Array.from({ length: half }, () => "positive"),
        ...Array.from({ length: n - half }, () => "negative")
      ];
      return this.shuffleWithSeed(base, seed ^ 0xA53);
    },

    // balance distal types as evenly as possible across n
    makeDistalArray(distalTypes, n, seed) {
      // e.g. if types are [natural, non-deliberate, deliberate]
      // repeat them until length n, then shuffle
      const base = [];
      let i = 0;
      while (base.length < n) {
        base.push(distalTypes[i % distalTypes.length]);
        i++;
      }
      return this.shuffleWithSeed(base, seed ^ 0xB91);
    },

    // attention check trial that looks like a normal vignette
    makeAttentionCheckTrial(valence) {
      const milkEvent = "During the day, someone bought milk at the grocery store.";
      const instructionEvent =
        "Attention check: Please select the event (A–D) that mentions milk.";

      // Put “mentions milk” in exactly one of B–D (and instruction in A, like prof suggested)
      const eventA = instructionEvent;
      const eventB = milkEvent; // correct should be B
      const eventC = "Later, the person walked home and put the groceries away.";
      const eventD = "In the evening, they cooked dinner and watched a movie.";

      const outcomePos = "Everything went normally and the evening was calm.";
      const outcomeNeg = "Nothing unusual happened, but the day was a bit tiring.";

      return {
        is_attention_check: true,
        scenario_id: "__attention_check__",
        scenario_title: "Story",
        distal_type: "__attention_check__",
        image: "",

        event_A: eventA,
        event_B: eventB,
        event_C: eventC,
        event_D: eventD,

        valence,
        outcome_shown: valence === "positive" ? outcomePos : outcomeNeg,
        attention_correct: "B"
      };
    }
  },

  async mounted() {
    try {
      const res = await fetch("stimuli/vignettes.csv");
      if (!res.ok) throw new Error(`Failed to fetch CSV: ${res.status}`);

      const csvText = await res.text();
      const parsed = Papa.parse(csvText, { header: true, skipEmptyLines: true });

      if (parsed.errors?.length) {
        console.warn("CSV parse warnings:", parsed.errors);
      }

      const rows = parsed.data.filter((r) => r.scenario_id);
      if (!rows.length) throw new Error("CSV loaded but has no rows with scenario_id.");

      // scenarios in CSV order (unique)
      const scenarioOrder = [];
      const seen = new Set();
      for (const r of rows) {
        if (!seen.has(r.scenario_id)) {
          seen.add(r.scenario_id);
          scenarioOrder.push(r.scenario_id);
        }
      }

      // group rows by scenario_id
      const byScenario = rows.reduce((acc, row) => {
        (acc[row.scenario_id] ||= []).push(row);
        return acc;
      }, {});

      const pid = this.getParticipantId();
      const seed = this.pidSeed(pid);

      // randomize trial order per participant
      const shuffledScenarios = this.shuffleWithSeed(scenarioOrder, seed ^ 0x111);

      const n = shuffledScenarios.length;

      // distal types available in CSV (unique)
      const distalTypes = Array.from(
        new Set(rows.map((r) => r.distal_type).filter(Boolean))
      );

      // If CSV has no distal_type values, we still run (but won’t “balance” them)
      const distalArray = distalTypes.length
        ? this.makeDistalArray(distalTypes, n, seed)
        : Array.from({ length: n }, () => "");

      const valenceArray = this.makeValenceArray(n, seed);

      // build trials: pick row that matches assigned distal_type (if possible)
      const builtTrials = shuffledScenarios.map((sid, i) => {
        const candidates = byScenario[sid] || [];
        if (!candidates.length) throw new Error(`No rows found for scenario_id="${sid}"`);

        const assignedDistal = distalArray[i];
        const match =
          assignedDistal && candidates.some((c) => c.distal_type === assignedDistal)
            ? candidates.find((c) => c.distal_type === assignedDistal)
            : candidates[0]; // fallback

        const valence = valenceArray[i];
        const outcomeShown =
          valence === "positive" ? match.outcome_positive : match.outcome_negative;

        return {
          ...match,
          valence,
          outcome_shown: outcomeShown,
          is_attention_check: false
        };
      });

      // Insert ONE attention check at a random-ish position (not first)
      const acValence = valenceArray[Math.floor(n / 2)] || "negative";
      const attentionTrial = this.makeAttentionCheckTrial(acValence);
      const insertAt = Math.min(Math.max(1, (seed % n)), builtTrials.length); // 1..n-1
      builtTrials.splice(insertAt, 0, attentionTrial);

      this.trials = builtTrials;

      // responses array aligned with trials
      this.responses = this.trials.map((t) => ({
        is_attention_check: !!t.is_attention_check,
        scenario_id: t.scenario_id,
        scenario_title: t.scenario_title,
        distal_type: t.distal_type,
        valence: t.valence,
        causeChoice: "",
        attention_correct: t.is_attention_check ? t.attention_correct : ""
      }));

      console.log("TRIALS SHOWN:", this.trials.map((t) => [t.scenario_id, t.distal_type, t.valence]));
    } catch (err) {
      console.error(err);
      this.errorMessage = String(err.message || err);
    } finally {
      this.loading = false;
    }
  }
};
</script>

